import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertService } from '../@core/service/alert.service';
import { DataStoreService } from '../@core/service/data-store.service';
import { RBACINFO } from '../@core/urls/rbac-url.config';
import { RbacService } from '../@core/service/rbac.service';
declare const $: any;

@Component({
  selector: 'policygroups',
  templateUrl: './policygroups.component.html',
  styleUrls: ['./policygroups.component.scss']
})
export class PolicygroupsComponent implements OnInit {
  RBACORG: RBACINFO = new RBACINFO();
  policyGroupList: any;
  filteredPolicyGroupList: any[] = [];
  individuals!: any[];
  managementGroups!: any[];
  modelPolicyName: string = '';
  policyGroupForm!: FormGroup;
  policyGroupId!: number;
  userGroupCount!: number;
  deletedId!: string;
  attachedUsers: any;
  formSubmit: boolean;
  groupid!: string;
  orgId: any;
  httpService:any;
  constructor(
    private fb: FormBuilder,
    private alertService: AlertService,
    private groupsService: RbacService,
    private _storeservice: DataStoreService
  ) {
    this.formSubmit = false;
    this.initializeForm();
  }

  ngOnInit(): void {
    this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        this.httpService = res['HTTPSERVICE']
      if (this.orgId) {
        this.getPolicyGroupList();
        this.getManagementGroupTree(this.orgId);
      }
      }
    });
  }
  initializeForm() {
    this.policyGroupForm = this.fb.group({
      id: [null],
      policygroupname: ['', Validators.required],
      description: ['']
    });
  }

  get formValidate() {
    return this.policyGroupForm.controls;
  }

  getPolicyGroupList(_managementGroupId?: number) {
    if (this.orgId) {
      this.groupsService.getOrgPolicyGroupList(this.orgId).subscribe(
        (res: any) => {
          this.policyGroupList = res['data'];
          this.filteredPolicyGroupList = this.policyGroupList;
        },
        (err: any) => console.log(err)
      );
    }
  }

  getManagementGroupTree(organizationid: any) {
    this.groupsService.getManagementGroupTree(organizationid).subscribe((items: any) => {
      this.managementGroups = items.data;
    });
  }

  searchPolicyGroup(event: Event) {
    const value = (event.target as HTMLInputElement).value.toLowerCase();
    this.filteredPolicyGroupList = this.policyGroupList.filter((a: any) =>
      a?.policygroupname?.toLowerCase().startsWith(value)
    );
  }

  getPolicyGroupInfo(policyGroup: any) {
    this.policyGroupId = policyGroup.id;
    this.groupsService.getPolicyGroupById(this.policyGroupId).subscribe((res: any) => {
      res['data'].managementgroupid = this.managementGroups.find(item => item.id === res['data'].managementgroupid);
      this.policyGroupForm.patchValue(res['data']);
      this.attachedUsers = res['data'].users;
    });
  }

  addPolicyGroup() {
    const requestBody = [];
    this.formSubmit = true;
    let data = this.policyGroupForm.getRawValue();
    data = Object.assign({ organizationid: this.orgId }, data);
    requestBody.push(data);
    if (this.policyGroupForm.valid) {
      if (this.policyGroupId) {
        this.groupsService.updatePolicyGroup(this.policyGroupId, data).subscribe(
          () => {
            this.getPolicyGroupList();
            this.alertService.success('Policy Group updated successfully');
          },
          (_err: any) => this.alertService.error('Failed to update Policy Group')
        );
      } else {
        this.groupsService.createPolicyGroup(requestBody).subscribe(
          () => {
            this.cancel();
            this.getPolicyGroupList();
            this.alertService.success('Policy Group created successfully');
          },
          (err: any) => {
            if (err?.error) {
              this.alertService.error(err?.error?.message);
            } else {
              this.alertService.error('Failed to add Policy Group');
            }
          }
        );
      }
    }
  }

  delete(event: Event, id: any) {
    event.stopPropagation();
    this.deletedId = id;
    $('#Deleteuser').modal('show');
  }

  cancel() {
    this.formSubmit = false;
    this.policyGroupForm.reset();
    this.policyGroupForm.markAsUntouched();
    this.policyGroupForm.markAsPristine();
    this.attachedUsers = [];
    this.policyGroupId = 0;
  }

  deletePolicyGroup() {
    this.groupsService.deletePolicyGroup(this.deletedId).subscribe(
      () => {
        this.cancel();
        this.alertService.success('Policy Group deleted successfully');
        $('#Deleteuser').modal('hide');
        this.getPolicyGroupList();
      },
      (_err: any) => this.alertService.error('Failed to delete Policy Group')
    );
  }
  onModelPolicy(value: string): void {
    this.modelPolicyName = value.replace(/\w\S*/g, (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase());
  }
}
