import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-rbac-policygroups/@core/permissions/permission.store';
import { DataStoreService } from './pics-rbac-policygroups/@core/service/data-store.service';
import { RBACINFO } from './pics-rbac-policygroups/@core/urls/rbac-url.config';

@Component({
  selector: 'rbac-policygroups',
  template: `
     <policygroups></policygroups>
  `,
  styles: [
  ]
})
export class RbacPolicygroupsComponent implements OnInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public groupEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.groupEvent.subscribe(val => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.httpService);
    })
  }

}
